      <label for="popularDetail" class="control-label">机构名称：</label>
      <div class="control-gtoup control-gtoup_w">
        <el-input v-model="input" placeholder="请输入内容"></el-input>
      </div>
    </div>
    <div class="form-group clearfix">
      <label for="popularDetail" class="control-label">机构介绍：</label>
      <div class="control-gtoup control-gtoup_w">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4}"
          placeholder="请输入内容"
          v-model="textarea3"
        ></el-input>
      </div>
    </div>
    <div class="form-group clearfix">
      <label for="popularDetail" class="control-label">所属地区：</label>
      <div class="control-gtoup">
        <template>
          <el-radio v-model="radio" label="1">国内</el-radio>
          <el-radio v-model="radio" label="2">国外</el-radio>
        </template>
      </div>
    </div>
    <div class="form-group clearfix">
      <label for="popularDetail" class="control-label">机构LOGO：</label>
      <div class="control-gtoup">
           <template>
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="true"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
         </template>
      </div>
    </div>
    <div class="btn-box">
      <el-button>发布</el-button>
      <el-button>保存</el-button>
      <el-button>删除</el-button>
    </div>
  </div>
</template>
      </div>
    </div>
  </div>
</template>

<script>
export default {
